import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import debounce from 'lodash/debounce';

const Container = styled.div`
  width: calc(100% - 72px);
  height: 100%;
  padding: 36px;
  background: green;
  text-align: center;
  margin: 0 auto;
`;

const Input = styled.input`
  width: 90%;
  // height: 100%;
  height: 28px;
  font-size: inherit;
  border: unset;
  border-radius: 5px;
  flex: 1;
  background: pink;
  padding: 8px;
`;

/*
 * The only thing it should manage is the state of the input bar
 */
class SearchInput extends Component {
  constructor() {
    super();
    this.state = {
      inputValue: '',
    };
  }

  changeInputValue = inputValue => this.setState({ inputValue }, () => this.onSearch());
  onChange = ({ target }) => this.changeInputValue(target.value);
  onKeyPress = ({ key }) => key === 'Enter' && this.onSearch();

  onSearch = debounce(() => {
    const { recordRecentlySearchedTerms } = this.props;
    recordRecentlySearchedTerms();
  }, 750);

  // NOTE: not using this anymore, no more search button
  renderSearchButton = () => {
    return <button
      onClick={() => this.onKeyPress({ key: 'Enter' })}
    >SEARCH</button>;
  }

  renderClearButton = () => {
    return <button
      onClick={() => this.changeInputValue('')}
    >CLEAR</button>;
  }

  renderAutoSuggestion = () => {

  }

  render() {
    const { inputValue } = this.state;
    // TODO: Placeholder should be a prop down, also most of these should be props down
    return <Container>
      <Input
        type='text'
        id='search-input'
        name='search'
        maxlength='30'
        autocorrect='off'
        autocapitalize='off'
        spellcheck='false'
        placeholder='Êº¢Â≠ó„ÇíÊé¢„Åó„Å¶„ÅÑ„Çã'
        value={inputValue}
        onChange={this.onChange}
        onKeyPress={this.onKeyPress}
        required={true}
      />
      {this.renderClearButton()}
    </Container>;
  }
}

SearchInput.propTypes = {
  recordRecentlySearchedTerms: PropTypes.func,
};

export default SearchInput;
