import React, { Component } from 'react';
import styled from 'styled-components';
import { MODAL_TYPES } from '../../constants';

const Container = styled.div`
  width: 100%;
  padding: 18px 0;
  height: 50px;
  background: papayawhip;
  text-align: center;
`;

const NavContents = styled.div`
  margin: 0 auto;
  width: 100%;
  background: blue;
  height: 100%;
`;

/*
 * When push we add new content to the modalContent
 * Modal detects change and will activate, so actually modal lives on the base app.
 *
 */

class TopNav extends Component {
  constructor(props) {
    super(props);
  }

  // TODO: make global settings shown state
  showSettings = (extraProps) => {
    this.props.addModal(MODAL_TYPES.SETTINGS, {});
  }

  render() {
    return <Container>
      <NavContents>
        <div>LOGO</div>
        <button type="button" onClick={this.showSettings}>SETTINGS</button>
      </NavContents>
    </Container>;
  }
}

export default TopNav;
