import React from 'react';
import { connect } from 'react-redux';
import Settings from './Settings';
import RadioOptions from './RadioOptions';
import KanjiModal from './KanjiModal';

const MODAL_TYPES = {
 SETTINGS: Settings,
 RADIO_OPTIONS: RadioOptions,
 KANJI_MODAL: KanjiModal,
};

const ModalRoot = ({ modalList }) => {
  if (!modalList.length) return null;
  const { modalType, extraProps } = modalList[modalList.length - 1];
  const SpecificModal = MODAL_TYPES[modalType];
  return <SpecificModal {...extraProps} />;
};

export default connect(
  state => state.modal,
)(ModalRoot);
