import { combineReducers } from 'redux';
import {
  UPDATE_KANJI_ORDER,
  UPDATE_KANJI_GROUP,
  UPDATE_KANJI_LEVEL,
  ADD_MODAL,
  POP_MODAL,
} from '../actions';
import { KANJI_ORDERS, KANJI_GROUPS } from '../constants';

const kanjiSettingDefaultState = {
  order: KANJI_ORDERS.RTK,
  group: KANJI_GROUPS.GRADE,
  level: null,

  // FUTURE STATES //
  theme: null,
  // FUTURE STATES //
};

const modalDefaultState = {
  modalList: [],
};

const kanjiSetting = (state = kanjiSettingDefaultState, action) => {
  if (typeof state === 'undefined') return kanjiSettingDefaultState;
  let newState = state;
  switch (action.type) {
    case UPDATE_KANJI_ORDER:
      newState = Object.assign({}, state, { order: action.order });
      break;
    case UPDATE_KANJI_GROUP:
      newState = Object.assign({}, state, { group: action.group });
      break;
    case UPDATE_KANJI_LEVEL:
      newState = Object.assign({}, state, { level: action.level });
      break;
    default:
      break;
  }
  return newState;
};

// NOTE: Prevents adding another of the same modal type
const addModalInternal = (modalList, modal) => {
  if (modalList.every(existingModal => modal.modalType !== existingModal.modalType)) {
    modalList.push(modal);
  }
};

const modal = (state = modalDefaultState, action) => {
  if (typeof state === 'undefined') return modalDefaultState;
  const modalList = [...state.modalList];

  switch (action.type) {
    case ADD_MODAL:
      addModalInternal(modalList, action.modal);
      break;
    case POP_MODAL:
      modalList.pop();
      break;
    default:
      break;
  }
  return { modalList };
};

export default combineReducers({
  kanjiSetting,
  modal,
});
