import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import { popModal } from '../../actions';

const ModalContainer = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
`;

const ModalBackground = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
`;

const ModalContent = styled.div`
  position: fixed;
  background: white;
  width: 80%;
  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 0;
  border-radius: 5px;
  box-shadow: 0 2px 6px 0 hsla(0, 0%, 0%, 0.2);
`;

export const ModalContentContainer = styled.div`
  padding: 18px;
  font-size: 14px;
`;

export const ModalContentRow = styled.div`
  width: 100%;
  height: 25px;

  .settings-label {
    float: left;
  }

  .settings-value {
    float: right;
    font-weight: 600;
  }

  .settings-description {
    color: hsl(0, 0%, 45%);
  }
`;

/*
 * TODO: move this to like a shared components folder, since this aint a feature
 * Modal becomes stackable, implement using store
 * Background click should always
 *
 * https://stackoverflow.com/questions/35623656/how-can-i-display-a-modal-dialog-in-redux-that-performs-asynchronous-actions
 */
class Modal extends Component {
  constructor(props) {
    super(props);
  }

  static propTypes = {
    backgroundClick: PropTypes.func,
  };

  popModalOnEsc = event => event.keyCode === 27 && this.props.popModal();

  componentDidMount() {
    document.addEventListener('keydown', this.popModalOnEsc);
  }

  componentWillUnmount() {
    document.removeEventListener('keydown', this.popModalOnEsc);
  }

  render() {
    const { children, popModal } = this.props;
    return (
      <ModalContainer>
        <ModalBackground onClick={popModal} />
        <ModalContent>{children}</ModalContent>
      </ModalContainer>
    );
  };
}

const mapStateToProps = () => ({});
const mapDispatchToProps = dispatch => ({
  popModal: () => dispatch(popModal()),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Modal);
