import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ModalBase, { ModalContentContainer, ModalContentRow } from './Modal';
import { popModal } from '../../actions';

class RadioOptions extends Component {
  constructor(props) {
    super(props);
  }

  static propTypes = {
    options: PropTypes.object,
    onChangeCallback: PropTypes.func,
  };

  renderOptions = (options, optionCallback) => {
    const listElements = [];
    for (const [key, value] of Object.entries(options)) {
      const listElement = (
        <ModalContentRow key={`modal-${key}`}>
          <input
            key={`input-${key}`}
            type="radio"
            value={value.value || key}
            onChange={event => optionCallback(event, value)}
          />
          <label htmlFor={`label-${key}`}>{value.label}</label>
        </ModalContentRow>
      );
      listElements.push(listElement);
    }
    return <ModalContentContainer>{listElements}</ModalContentContainer>;
  };

  render() {
    const { options, onChangeCallback } = this.props;
    const optionComponents = this.renderOptions(options, onChangeCallback);
    return (
      <ModalBase>
        {optionComponents}
      </ModalBase>
    );
  }
}

const mapDispatchToProps = dispatch => ({
  popModal: () => dispatch(popModal()),
});

const mapStateToProps = () => ({});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(RadioOptions);
