import React, { Component } from 'react';
import { connect } from 'react-redux';
import {
  changeKanjiOrdering,
  changeKanjiGrouping,
  changeKanjiLevel,
  addModal,
  popModal,
} from '../../actions';
import { KANJI_ORDERS, KANJI_GROUPS } from '../../constants';
import ModalBase, { ModalContentContainer, ModalContentRow } from './Modal';
import { MODAL_TYPES } from '../../constants';

const mapStateToProps = state => ({
  currentGroup: state.kanjiSetting.group,
  currentOrder: state.kanjiSetting.order,
  currentLevel: state.kanjiSetting.level,
});

const mapDispatchToProps = dispatch => {
  return {
    addModal: (modalType, extraProps) => dispatch(addModal(modalType, extraProps)),
    popModal: () => dispatch(popModal()),
    onKanjiGroupingChange: group => dispatch(changeKanjiGrouping(group)),
    onKanjiOrderingChange: order => dispatch(changeKanjiOrdering(order)),
    onKanjiLevelChange: level => dispatch(changeKanjiLevel(level)),
  };
};

class Settings extends Component {
  constructor(props) {
    super(props);
  }

  onChangeFactory = (lookup, optionCallback) => (event, value) => {
    const key = event.target.value;
    (lookup && optionCallback(lookup[key])) || optionCallback(value);
    this.props.popModal();
  }

  renderKanjiOrdering = () => {
    const { currentOrder, addModal, onKanjiOrderingChange } = this.props;
    const changeKanjiOrdering = () => {
      addModal(MODAL_TYPES.RADIO_OPTIONS, {
        onChangeCallback: this.onChangeFactory(KANJI_ORDERS, onKanjiOrderingChange),
        options: KANJI_ORDERS,
      });
    };
    return this.renderOptionSettings('Order by', currentOrder.label, null, changeKanjiOrdering);
  }

  renderKanjiGrouping = () => {
    const { currentGroup, addModal, onKanjiGroupingChange } = this.props;
    const changeKanjiGrouping = () => {
      addModal(MODAL_TYPES.RADIO_OPTIONS, {
        onChangeCallback: this.onChangeFactory(KANJI_GROUPS, onKanjiGroupingChange),
        options: KANJI_GROUPS,
      });
    };
    return this.renderOptionSettings('Group by', currentGroup.label, currentGroup.description, changeKanjiGrouping);
  }

  renderKanjiLevel = () => {
    const { currentLevel, currentGroup, addModal, onKanjiLevelChange } = this.props;
    const currentLevelLabel = currentLevel ? currentLevel.label : currentGroup.defaultLevel.label;
    const changeKanjiLevel = () => {
      addModal(MODAL_TYPES.RADIO_OPTIONS, {
        onChangeCallback: this.onChangeFactory(null, onKanjiLevelChange),
        options: currentGroup.choices,
      });
    };
    return this.renderOptionSettings('Level to display', currentLevelLabel, null, changeKanjiLevel);
  }

  renderTheming = () => {
    return (
      <ModalContentRow>
        <div className='settings-label'>Theme</div>
        <div className='settings-value'>üåöüåù</div>
      </ModalContentRow>
    );
  }

  renderOptionSettings = (title, value, description, onClick) => {
    const descriptionRow = description ? <ModalContentRow>
      <div className='settings-description'>{description}</div>
    </ModalContentRow> : null;
    return (
      <React.Fragment>
        <ModalContentRow>
          <div className='settings-label'>{title}</div>
          <div
            className='settings-value'
            onClick={onClick}
          >{value}</div>
        </ModalContentRow>
        {descriptionRow}
      </React.Fragment>

    );
  }

  render() {
    const { popModal } = this.props;
    return (
      <ModalBase>
        <ModalContentContainer>
          {this.renderKanjiOrdering()}
          {this.renderKanjiGrouping()}
          {this.renderKanjiLevel()}
          {this.renderTheming()}
          <ModalContentRow>
            <div className='settings-value'>
              <button onClick={popModal}>OK</button>
            </div>
          </ModalContentRow>
        </ModalContentContainer>
      </ModalBase>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Settings);
