import { connect } from 'react-redux';
import {
  changeKanjiOrdering,
  changeKanjiGrouping,
  addModal,
} from '../../actions';
import TopNav from './TopNav';

/*
 * NOTE: Passes onchange to search input. To be index.js
 * What can this container do?
 * - Talk to central store for recently changed values, get auto suggestions
 * - Provide an onchange
 * - Save UI states in regards to input bar (currently searching or something)
 */

const mapStateToProps = state => ({
  currentGroup: state.kanjiSetting.group,
  currentOrder: state.kanjiSetting.order,
});

const mapDispatchToProps = dispatch => {
  return {
    addModal: (modalType, extraProps) => dispatch(addModal(modalType, extraProps)),
    onKanjiGroupingChange: group => dispatch(changeKanjiGrouping(group)),
    onKanjiOrderingChange: order => dispatch(changeKanjiOrdering(order)),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(TopNav);
