import { connect } from 'react-redux';
import KanjisContainer from './KanjisContainer';
import { addModal } from '../../actions';

const mapStateToProps = state => ({
  currentGroup: state.kanjiSetting.group,
  currentOrder: state.kanjiSetting.order,
  currentLevel: state.kanjiSetting.level,
});

const mapDispatchToProps = dispatch => ({
  addModal: (modalType, extraProps) => dispatch(addModal(modalType, extraProps)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(KanjisContainer);
