import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';

const KanjiContainer = styled.div`
  display: inline-block;
  margin: 2px;
  padding: 5px;
  background: red;
  border-radius: 5px;
`;

const Kanji = ({ kanji, value }) => {
  return (
    <KanjiContainer key={value} data-id={value} data-kanji={kanji}>
      {kanji}
    </KanjiContainer>
  );
};

Kanji.propTypes = {
  kanji: PropTypes.string,
  id: PropTypes.number,
}

export default Kanji;
